@import "vars";
@import "mixins";

.vision {
  margin-top: -50px;

  &__container {
    background-image: url("../images/vision/vps-bg.svg");
    background-size: contain;
    background-repeat: no-repeat;
  }

  &__content {
    max-width: 754px;
    text-align: center;
    margin: auto;
  }

  &__title {
    font-weight: 900;
    font-size: 48px;
    line-height: 48px;
    color: $heavy-blue;
    margin-top: 160px;
    margin-bottom: 57px;
  }

  &__subtitle {
    font-weight: 600;
    font-size: 24px;
    line-height: 36px;
    color: $heavy-blue-definition;
    margin-bottom: 55px;
  }

  &__button {
    @include blue-button;
    margin-bottom: 256px;
  }

  @media (max-width: 999px) {
    & {

      &__container {
        background: none;
      }

      &__button {
        margin-bottom: 120px;
      }
    }
  }
}
